import React from 'react';
import Header from './components/common/Header';

import './assets/css/desktop.css';
import './assets/css/mobile.css';
import './assets/css/tablet.css';
import Footer from './components/common/Footer';
import Home from './components/pages/Home';
import globaltraining from './components/pages/Globaltraining';
import japanesetraining from './components/pages/Japanesetraining';
import businessmanagment from './components/pages/Businessmanagment';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import Businessanalyasis from './components/pages/Businessanalyasis1';
import Projectmcourse from './components/pages/Projectmcourse';
import Tandengcourse from './components/pages/Tandengcourse';
import Competency from './components/pages/Competency';
import Projectmang from './components/pages/Projectmanag';
import Globalbusiness from './components/pages/Globalbusiness';
import Agile1 from './components/pages/Agile1';
import Cloudcomputing from './components/pages/Cloudcomputing';
import Netsec from './components/pages/Netsec';
import Aitraining from './components/pages/Aitraining';
import AI from './components/pages/AI';
import Aitech from './components/pages/Aitech';
import Aboutus from './components/pages/Aboutus';
import Trainingfeature from './components/pages/Trainingfeature';
function App() {
  return (
    <BrowserRouter>
      <div>
        <Header /> 

        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/globaltraining" component={globaltraining} />
          <Route path="/japanesetraining" component={japanesetraining} />
          <Route path="/businessmanagement" component={businessmanagment} />
          <Route path="/businessanalyasis" component={Businessanalyasis} />
          <Route path="/projectmcourse" component={Projectmcourse} />
          <Route path="/techandeng" component={Tandengcourse} />
          <Route path="/competency" component={Competency} />
          <Route path="/projmang" component={Projectmang} />
          <Route path="/gbusiness" component={Globalbusiness} />
          <Route path="/agile" component={Agile1} />
          <Route path="/cloudcomputing" component={Cloudcomputing} />
          <Route path="/networking" component={Netsec} />
          <Route path="/aibusiness" component={Aitraining} />
          <Route path="/ai" component={AI} />
          <Route path="/aitech" component={Aitech} />
          <Route path="/aboutus" component={Aboutus} />
          <Route path="/trainingfeature" component={Trainingfeature} />
       
         
        </Switch>

       
        <Footer />
      </div >
  </BrowserRouter>




  );
}

export default App;
