import React, { Component } from 'react';
import { Container } from 'react-bootstrap';
// import Partical from './Partical';
import Particles from 'react-particles-js';

var sectionStyle = {
  width: "100%",
  height: "600px",
  // backgroundImage:`url(${globalhub} )`,  
  // backgroundSize:"100%"
  
  
  
};


/**
 * 
 *@author: Rohit Autade
@version 1.0 Jan 31, 2020
 * 
 */

class Vision extends Component {
    state = {  }

      /**
* 
* @usage this function render Vision Component.
* 
*/

    render() { 
        return (  
            <>
 {/* style={ sectionStyle } */}
        <div className="iti-container" >
         <div className="iti-vision iti-div"> <b className="iti-visionsize"> Aiming To The Global Hub</b><br/>
         <b className="iti-visiontext">With Education And Technology</b></div> 
            <div className="iti-back1">
         <div ><Particles height="590px"
        
    params={{
      
      "particles": {
        "number": {
          "value": 100,
          "density": {
            "enable": true,
            "value_area": 600,
            
          }
        },
        "color": {
          "value": ["#0000ff","#0000ff"]
        },
        "shape": {
          "type": "circle",
          "stroke": {
            "width": 0,
           
            "color": "#000000"
          },
          "polygon": {
            "nb_sides": 15
          }
        },
        "opacity": {
          "value": 1,
          "random": false,
          "anim": {
            "enable": true,
            "speed": 1,
            "opacity_min": 1,
            "sync": false
          }
        },
        "size": {
          "value": 5,
          "random": false,
          "anim": {
            "enable": true,
            "speed": 1,
            "size_min": 0.15,
            "sync": false
            
          }
        },
        "line_linked": {
          "enable": true,
          "distance": 210,
          "color": "#FFA500",
          "opacity": 0.25,
          "width": 2.5
        },
        "move": {
          "enable": true,
          "speed": 1,
          "direction": "none",
          "out_mode": "out",
          
         
        }
      },
      "interactivity": {
        "detect_on": "canvas",
        "events": {
          "onhover": {
            "enable": true,
            "mode": "repulse"
        },
          "onclick": {
            "enable": true,
            "mode": "push"
          },
          "resize": true
        },
        
        "modes": {
          
          "repulse": {
            "distance": 70,
            "duration": 0.4
          },
          "push": {
            "particles_nb": 2
          },
          
        }
      },
      "retina_detect": true
     
	}} /> </div>
                            </div></div>
               
           <Container>
           <br/>
             <br/>
           
                 <p className="iti-text">We believe <b>Globalization</b> is not about creating uniformity, 
                     but about accepting differences towards global achievements.
                      We are aiming to create a platform where each stakeholder from 
                      different backgrounds can be a key contributor and excel in their
                      respective arenas. This synergy of diversity can create better understanding
                      and innovate new solutions beyond borders and nations.</p>
           </Container>
           </>
        );
    }
}
 
export default Vision;