import React, { Component } from 'react';
import industries from "../../assets/images/iti-industries.jpeg";
import unipune from "../../assets/images/iti-puneUni.jpeg";
import punelife from "../../assets/images/iti-punelifestyle.jpg";
import { Container, Card, CardDeck } from 'react-bootstrap'

/**
 * 
 *@author: Rohit Autade
@version 1.0 Jan 31, 2020
 * 
 */

class History extends Component {
    state = {}
    /**
* 
* @usage this function render History Component.
* 
*/
    render() {
        return (
            <>
                <Container>

                    <div>
                        <br />
                        <h3 className="text-center iti-titlecolor">History</h3>
                        <br />
                        <p className="iti-text"> IT innovation India Pvt. Ltd., has its head office in Japan (Tokyo).
                             It has rich experiences in IT consulting over 20 years and it started educational
                             services as a part of consulting for human resource development.
                             It was established for the purpose of meeting requirement to develop human resources
                              who can perform an active role globally. We selected Pune as our global training base,
                               as India is developing in the field of Information technology at a rapid pace. Pune is a well-established
                     IT city and educational hub with good lifestyle and environment.            <br></br></p>

                        <br />
                        <br />
                        <div>
                            <CardDeck>
                                <Card className="iti-shadow">
                                    <Card.Img variant="top" src={industries} height="160px" />
                                    <Card.Body>
                                        <Card.Title className="text-center"> Industry Presence </Card.Title>
                                        <>

                                            <ul>
                                                <li>
                                                    Partnership.
                                                    </li>
                                                <li>
                                                    Business Opportunities.
                                                    </li>
                                                <li>
                                                    Real Case Study.
                                                    </li>

                                            </ul>

                                        </>
                                    </Card.Body>
                                    <Card.Footer>
                                        <small className="text-muted">Lots of Industries</small>
                                    </Card.Footer>
                                </Card>
                                <Card className="iti-shadow">
                                    <Card.Img variant="top" src={unipune} height="160px" />
                                    <Card.Body>
                                        <Card.Title className="text-center">Academic Hub</Card.Title>
                                        <>
                                            <ul>
                                                <li>
                                                    Talent Availability.
</li>
                                                <li>
                                                    Partnership.
</li>
                                                <li>
                                                   Updated Contents.
</li>

                                            </ul>
                                        </>
                                    </Card.Body>
                                    <Card.Footer>
                                        <small className="text-muted">Oxford of the East</small>
                                    </Card.Footer>
                                </Card>
                                <Card className="iti-shadow"    >
                                    <Card.Img variant="top" src={punelife} height="160px" />
                                    <Card.Body>
                                        <Card.Title className="text-center">Life Environment</Card.Title>
                                        <>
                                            <ul>
                                                <li>
                                                    Politically Stable.
</li>
                                                <li>
                                                    Infrastructure.
</li>
                                                <li>
                                                    Cost Effectiveness.
</li>

                                            </ul>

                                        </>
                                    </Card.Body>
                                    <Card.Footer>
                                        <small className="text-muted">Good Infrastructure</small>
                                    </Card.Footer>
                                </Card>
                            </CardDeck>

                        </div>
                    </div>
                </Container>
            </>
        );
    }
}

export default History;